.container {
    margin: 120px;
    display: flex;
    justify-content: center;
}

.container-fluid {
    margin: 120px;
}

.row {
    display: flex;
    justify-content: space-between;   
}

.header {
    font-size: 32px;
    font-weight: 600;
    text-align: center;
}

.form {
    margin: auto;

    .title {
        margin: 32px 0 0 0;
        text-align: center;
        font-size: 24px;
        font-weight: 600;
    }

    .roles {
        display: flex;
        justify-content: space-around;
        margin: 54px 0;
        align-items: center;

        .role {
            img {
                border-radius: 50%;
            }
            .role-label {
                text-align: center;
                margin: 12px;
                font-size: 18px;
            }
        }
    }

    .next-step {
        display: flex;
        justify-content: center;
        margin: 0 0 0 0;

        button {
            background: #1A3447;
            border-radius: 5px;
            color: white;
            width: 800px;
            height: 50px;
            font-size: 24px;
        }
    }
}

.form-title {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    color: #1A3447;
    margin: 12px 0;
}

.form-subtitle {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #1A3447;
    margin: 12px 0;
}

.form-body {
    border: 1px solid #000;
    padding: 25px;
    border-radius: 5px;
    margin: 0 0 12px 0;

    .input-field {
        input, select {
            width: 280px;
            margin: 10px 0;
            font-size: 18px;
            border-radius: 5px;
            border: 1px solid rgba(0, 0, 0, 0.4);
            padding: 10px;
        }
    }

    .form-title {
        color: #242731;
    }

    .input-field {
        input {
            width: 600px;
            margin: 10px 0;
            font-size: 18px;
            border-radius: 5px;
            border: 1px solid rgba(0, 0, 0, 0.4);
            padding: 10px;
        }
    }
    .input-check {
        margin: 6px 0;
        .plot-type {
            font-weight: 500;
            margin: 0px 0 6px 0px;
        }
        input {
            width: 100%;
        }
    }

    .form-footer {
        color: rgba(0, 0, 0, 0.4);
        display: flex;
        justify-content: center;
        font-size: 14px;
        margin: 6px 0 0 0;
    }
}

.navigate {
    display: flex;
    justify-content: space-between;

    .navigate-btn {
        margin: 12px 0 0 0;
        .grey {
            background: #B9B9C3;
            border-radius: 5px;
            width: 150px;
            padding: 8px 0;
            font-size: 18px;
            color: white;
        }
        .blue {
            background: #1A3447;
            border-radius: 5px;
            width: 150px;
            padding: 8px 0;
            font-size: 18px;
            color: white;
        }
    }
}

.btn-section {
    margin: 24px;
    .navigate-btn {
        margin: 12px 0 0 0;
        .blue {
            background: #1A3447;
            border-radius: 5px;
            width: 400px;
            padding: 18px 0;
            font-size: 18px;
            color: white;
        }
    }
}

.farmer-profile {
    margin: 24px;
    .details {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: 24px 0;
        img {
            border-radius: 50%;
            margin: 0 32px;
        }
        .name-section {
            font-weight: 700;
            font-size: 30px;
            color: #434343;
            margin: 0 32px;
        }
    }
}

.info {
    margin: 48px  24px;
    .col {
        margin: 12px 32px;
    }
}
