.container {
    margin: 90px 0px;
    display: flex;
}

.row {
    display: flex;
    justify-content: space-between;

    img {
        margin: 24px;
        border-radius: 15px;
    }
}

.navigate-btn {
    margin: auto;
    display: flex;
    justify-content: space-between;
    
    .grey {
        background: #B9B9C3;
        border-radius: 5px;
        width: 150px;
        padding: 8px 0;
        font-size: 18px;
        color: white;
    }
    .blue {
        background: #1A3447;
        border-radius: 5px;
        width: 150px;
        padding: 8px 0;
        font-size: 18px;
        color: white;
    }
}

.sidebar {
    border: 1px solid #000;
    width: 400px;
    height: 87vh;
    text-align: center;

    .title {
        margin: 25px;
        font-size: 26px;
        font-weight: 600;
    }

    .options {
        margin: 40px 0;

        .option {
            padding: 22px 0;
            border: 0.1em solid #000;
        }
        .option-select {
            padding: 22px 0;
            border: 0.1em solid #000;
            background-color: rgb(106, 126, 241);;
        }
    }
}

.body {
    margin: 60px;

    .info-container {
        // margin: 0 120px;

        .header {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 600;
            font-size: 36px;
            line-height: 44px;

            color: #1A3447;
        }
        .sub-header {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 300;
            font-size: 20px;
            line-height: 24px;
            margin: 10px 0;
            color: #1A3447;
        }
    }

    .table {
        background: #a2d2ff;
        box-shadow: 0px 10px 60px rgba(226, 236, 249, 0.5);
        border-radius: 30px;
        padding: 24px;
        margin: 32px 72px;
        
        .title {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 600;
            font-size: 22px;
            line-height: 33px;

            letter-spacing: -0.01em;

            color: #000000;
        }

        .display {
            margin: 32px;

            table {
                text-align: center;
            }
            table, th, td {
                border: 1px solid black;
                border-collapse: collapse;
                padding: 26px 42px;
            }
            button {
                background: rgba(22, 192, 152, 0.38);
                border: 1px solid #00B087;
                border-radius: 4px;
                align-items: center;
                padding: 4px 12px;
            }
        }
    }
}

.title {
    margin: 25px;
    font-size: 18px;
    font-weight: 600;
}

.subtitle {
    margin: 5px;
    font-size: 16px;
}

.slt-drones {
    background: #00A3FF;
    border-radius: 5px;
    cursor: pointer;
    padding: 12px;
    color: white;
}

.create-slt {
    background: #4F4F4F;
    border-radius: 4px;
    cursor: pointer;
    padding: 12px;
    color: white;
}

.form-body {
    border: 1px solid #000;
    padding: 25px;
    border-radius: 5px;
    margin: 0 0 12px 0;

    .input-field {
        input {
            width: 280px;
            margin: 10px 0;
            font-size: 18px;
            border-radius: 5px;
            border: 1px solid rgba(0, 0, 0, 0.4);
            padding: 10px;
        }
    }

    .form-title {
        color: #242731;
    }

    .input-field {
        input {
            width: 600px;
            margin: 10px 0;
            font-size: 18px;
            border-radius: 5px;
            border: 1px solid rgba(0, 0, 0, 0.4);
            padding: 10px;
        }
    }
    .input-check {
        margin: 6px 0;
        .plot-type {
            font-weight: 500;
            margin: 0px 0 6px 0px;
        }
        input {
            width: 100%;
        }
    }

    .form-footer {
        color: rgba(0, 0, 0, 0.4);
        display: flex;
        justify-content: center;
        font-size: 14px;
        margin: 6px 0 0 0;
    }
}


.month {
    padding: 70px 25px;
    width: 100%;
    background: #1abc9c;
    text-align: center;
  }
  
  .month ul {
    margin: 0;
    padding: 0;
  }
  
  .month ul li {
    color: white;
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3px;
  }
  
  .month .prev {
    float: left;
    padding-top: 10px;
  }
  
  .month .next {
    float: right;
    padding-top: 10px;
  }
  
  .weekdays {
    margin: 0;
    padding: 10px 0;
    background-color: #ddd;
  }
  
  .weekdays li {
    display: inline-block;
    width: 13.6%;
    color: #666;
    text-align: center;
  }
  
  .days {
    padding: 10px 0;
    background: #eee;
    margin: 0;
  }
  
  .days li {
    list-style-type: none;
    display: inline-block;
    width: 13.6%;
    text-align: center;
    margin-bottom: 5px;
    font-size:12px;
    color: #777;
  }
  
  .days li .active {
    padding: 5px;
    background: #1abc9c;
    color: white !important
  }